        <text class="empty-text">ÂºÄÂßã‰∏éAIËøõË°åÂøÉÁêÜÂÄæËØâÂØπËØù</text>
        <text class="empty-hint">Êàë‰ºöÂÄæÂê¨ÊÇ®ÁöÑÂøÉÂ£∞ÔºåÁªô‰∫àÊîØÊåÅÂíåÂª∫ËÆÆ</text>
      </view>
      
      <view v-for="(msg, index) in messages" :key="index" :id="getMsgId(index)" class="message-item">
        <!-- Áî®Êà∑Ê∂àÊÅØ -->
        <view v-if="msg.role === 'user'" class="message user-message">
