          </button>
        </view>
      </view>
      
      <!-- Ë°®ÊÉÖÈÄâÊã©Âô® -->
      <view v-if="showEmojiPicker" class="emoji-picker">
        <view class="emoji-grid">
          <view 
            v-for="(emoji, index) in emojiList" 
            :key="index" 
            class="emoji-item"
            @tap="insertEmoji(emoji)"
          >
            {{ emoji }}
          </view>
        </view>
      </view>
    </view>
    
    
  </view>
</template>

<script>
import tabBarManager from '@/utils/tabbar-manager.js';
import chatStorage from '@/utils/chat-storage.js';

export default {
  data() {
    return {
      messages: [],
      inputText: '',
      isSending: false,
      isTyping: false,
      scrollIntoView: '',
      msgIdPrefix: 'msg-',
      sessionId: 'default', // ‰ºöËØùIDÔºåÂèØÊâ©Â±ï‰∏∫Â§ö‰ºöËØù
      isLoadingHistory: false,
      favoriteMessages: [],  // Êî∂ËóèÁöÑÊ∂àÊÅØ
      showEmojiPicker: false,  // ÊòØÂê¶ÊòæÁ§∫Ë°®ÊÉÖÈÄâÊã©Âô®
      emojiList: [
        'üòä', 'üòÉ', 'üòÑ', 'üòÅ', 'üòÜ', 'üòÖ', 'ü§£', 'üòÇ',
        'üôÇ', 'üôÉ', 'üòâ', 'üòä', 'üòá', 'ü•∞', 'üòç', 'ü§©',
        'üòò', 'üòó', '‚ò∫Ô∏è', 'üòö', 'üòô', 'ü•≤', 'üòã', 'üòõ',
        'üòú', 'ü§™', 'üòù', 'ü§ë', 'ü§ó', 'ü§≠', 'ü§´', 'ü§î',
        'ü§ê', 'ü§®', 'üòê', 'üòë', 'üò∂', 'üòè', 'üòí', 'üôÑ',
        'üò¨', 'ü§•', 'üòå', 'üòî', 'üò™', 'ü§§', 'üò¥', 'üò∑',
        'ü§í', 'ü§ï', 'ü§¢', 'ü§Æ', 'ü§ß', 'ü•µ', 'ü•∂', 'üò∂‚Äçüå´Ô∏è',
        'üòµ', 'ü§Ø', 'ü§†', 'ü•≥', 'ü•¥', 'üòé', 'ü§ì', 'üßê',
        'üëç', 'üëé', 'üëè', 'üôè', 'üí™', '‚ù§Ô∏è', 'üíî', 'üíØ'
      ]
    }
  },
  
  async onLoad() {
    console.log('[CHAT] AIÂØπËØùÈ°µÈù¢Âä†ËΩΩ');
    
    // ÂàùÂßãÂåñÂ≠òÂÇ®
    await chatStorage.init();
    
    // Âä†ËΩΩÂéÜÂè≤Ê∂àÊÅØ
    await this.loadHistoryMessages();
    
    // Âä†ËΩΩÊî∂ËóèÂàóË°®
    this.loadFavorites();
    
    // Â¶ÇÊûúÊ≤°ÊúâÂéÜÂè≤Ê∂àÊÅØÔºåÊ∑ªÂä†Ê¨¢ËøéÊ∂àÊÅØ
    if (this.messages.length === 0) {
      this.addAIMessage('ÊÇ®Â•ΩÔºÅÊàëÊòØÊÇ®ÁöÑÂøÉÁêÜÊîØÊåÅAI„ÄÇÊó†ËÆ∫ÊÇ®ÈÅáÂà∞‰ªÄ‰πàÂõ∞Êâ∞ÔºåÈÉΩÂèØ‰ª•ÂíåÊàëÂÄæËØâ„ÄÇÊàë‰ºöËÆ§ÁúüÂÄæÂê¨ÔºåÂπ∂Â∞ΩÊàëÊâÄËÉΩÁªô‰∫àÊîØÊåÅÂíåÂª∫ËÆÆ„ÄÇ');
    }
    
    // Ê∏ÖÁêÜËøáÊúüÊï∞ÊçÆÔºàÂêéÂè∞ÊâßË°åÔºâ
    chatStorage.cleanExpiredData().catch(err => {
      console.warn('[CHAT] Ê∏ÖÁêÜËøáÊúüÊï∞ÊçÆÂ§±Ë¥•:', err);
    });
  },
  
  onShow() {
    // ÈÄöÁü•ÂØºËà™Ê†èÊõ¥Êñ∞Áä∂ÊÄÅ
    tabBarManager.setCurrentIndexByPath('/pages/intervene/chat');
  },
  
  onUnload() {
    // È°µÈù¢Âç∏ËΩΩÊó∂‰øùÂ≠òÊ∂àÊÅØ
    this.saveAllMessages();
  },
  
  methods: {
    /**
     * Âä†ËΩΩÂéÜÂè≤Ê∂àÊÅØ
     */
    async loadHistoryMessages() {
      try {
        this.isLoadingHistory = true;
        const messages = await chatStorage.getMessages(this.sessionId);
        
        if (messages && messages.length > 0) {
          // ËΩ¨Êç¢‰∏∫È°µÈù¢‰ΩøÁî®ÁöÑÊ†ºÂºè
          this.messages = messages.map(msg => ({
            role: msg.role,
            content: msg.content,
            timestamp: msg.timestamp
          }));
          
          console.log(`[CHAT] Â∑≤Âä†ËΩΩ ${messages.length} Êù°ÂéÜÂè≤Ê∂àÊÅØ`);
          this.scrollToBottom();
        }
      } catch (error) {
        console.error('[CHAT] Âä†ËΩΩÂéÜÂè≤Ê∂àÊÅØÂ§±Ë¥•:', error);
      } finally {
        this.isLoadingHistory = false;
      }
    },
    
    /**
     * ‰øùÂ≠òÂçïÊù°Ê∂àÊÅØ
     */
    async saveMessage(message) {
      try {
        await chatStorage.saveMessage(this.sessionId, {
          role: message.role,
          content: message.content,
          timestamp: message.timestamp || Date.now()
        });
      } catch (error) {
        console.error('[CHAT] ‰øùÂ≠òÊ∂àÊÅØÂ§±Ë¥•:', error);
      }
    },
    
    /**
     * ‰øùÂ≠òÊâÄÊúâÊ∂àÊÅØ
     */
    async saveAllMessages() {
      try {
        await chatStorage.saveMessages(this.sessionId, this.messages);
        console.log('[CHAT] ÊâÄÊúâÊ∂àÊÅØÂ∑≤‰øùÂ≠ò');
      } catch (error) {
        console.error('[CHAT] ‰øùÂ≠òÊâÄÊúâÊ∂àÊÅØÂ§±Ë¥•:', error);
      }
    },
    
    /**
     * Ê∏ÖÁ©∫ÂΩìÂâç‰ºöËØù
     */
    async clearCurrentSession() {
      try {
        await chatStorage.clearSession(this.sessionId);
        this.messages = [];
        
        // ÈáçÊñ∞Ê∑ªÂä†Ê¨¢ËøéÊ∂àÊÅØ
        this.addAIMessage('ÊÇ®Â•ΩÔºÅÊàëÊòØÊÇ®ÁöÑÂøÉÁêÜÊîØÊåÅAI„ÄÇÊó†ËÆ∫ÊÇ®ÈÅáÂà∞‰ªÄ‰πàÂõ∞Êâ∞ÔºåÈÉΩÂèØ‰ª•ÂíåÊàëÂÄæËØâ„ÄÇÊàë‰ºöËÆ§ÁúüÂÄæÂê¨ÔºåÂπ∂Â∞ΩÊàëÊâÄËÉΩÁªô‰∫àÊîØÊåÅÂíåÂª∫ËÆÆ„ÄÇ');
        
        uni.showToast({
          title: 'ËÅäÂ§©ËÆ∞ÂΩïÂ∑≤Ê∏ÖÁ©∫',
          icon: 'success'
        });
        
        console.log('[CHAT] ‰ºöËØùÂ∑≤Ê∏ÖÁ©∫');
      } catch (error) {
        console.error('[CHAT] Ê∏ÖÁ©∫‰ºöËØùÂ§±Ë¥•:', error);
        uni.showToast({
          title: 'Ê∏ÖÁ©∫Â§±Ë¥•',
          icon: 'none'
        });
      }
    },
    
    /**
     * Â§ÑÁêÜÊ∂àÊÅØÈïøÊåâ
     */
    handleLongPress(msg, index) {
      const isFavorite = msg.isFavorite || false;
      const actions = ['Â§çÂà∂Ê∂àÊÅØ', isFavorite ? 'ÂèñÊ∂àÊî∂Ëóè' : 'Êî∂ËóèÊ∂àÊÅØ', 'Âà†Èô§Ê∂àÊÅØ'];
      
      uni.showActionSheet({
        itemList: actions,
        success: (res) => {
          const actionIndex = res.tapIndex;
          
          switch (actionIndex) {
            case 0:
              // Â§çÂà∂Ê∂àÊÅØ
              this.copyMessage(msg);
              break;
            case 1:
              // Êî∂Ëóè/ÂèñÊ∂àÊî∂ËóèÊ∂àÊÅØ
              this.toggleFavoriteMessage(msg, index);
              break;
            case 2:
              // Âà†Èô§Ê∂àÊÅØ
              this.deleteMessage(index);
              break;
          }
        }
      });
      
      // ÈúáÂä®ÂèçÈ¶à
      uni.vibrateShort({
        success: () => {
          console.log('[CHAT] ÈïøÊåâÈúáÂä®ÂèçÈ¶à');
        }
      });
    },
    
    /**
     * Â§çÂà∂Ê∂àÊÅØÂÜÖÂÆπ
     */
    copyMessage(msg) {
      uni.setClipboardData({
        data: msg.content,
        success: () => {
          uni.showToast({
            title: 'Â∑≤Â§çÂà∂',
            icon: 'success',
            duration: 1500
          });
          console.log('[CHAT] Ê∂àÊÅØÂ∑≤Â§çÂà∂');
        },
        fail: (err) => {
          console.error('[CHAT] Â§çÂà∂Â§±Ë¥•:', err);
          uni.showToast({
            title: 'Â§çÂà∂Â§±Ë¥•',
            icon: 'none'
          });
        }
      });
    },
    
    /**
     * ÂàáÊç¢Ê∂àÊÅØÊî∂ËóèÁä∂ÊÄÅ
     */
    toggleFavoriteMessage(msg, index) {
      const isFavorite = msg.isFavorite || false;
      
      // Êõ¥Êñ∞Ê∂àÊÅØÁä∂ÊÄÅ
      this.$set(this.messages[index], 'isFavorite', !isFavorite);
      
      // Êõ¥Êñ∞Êî∂ËóèÂàóË°®
      if (!isFavorite) {
        // Ê∑ªÂä†Âà∞Êî∂Ëóè
        this.favoriteMessages.push({
          content: msg.content,
          role: msg.role,
          timestamp: msg.timestamp || Date.now(),
          sessionId: this.sessionId
        });
        
        uni.showToast({
          title: 'Â∑≤Êî∂Ëóè',
          icon: 'success',
          duration: 1500
        });
        console.log('[CHAT] Ê∂àÊÅØÂ∑≤Êî∂Ëóè');
      } else {
        // ‰ªéÊî∂Ëóè‰∏≠ÁßªÈô§
        const favIndex = this.favoriteMessages.findIndex(
          fav => fav.content === msg.content && fav.timestamp === msg.timestamp
        );
        if (favIndex > -1) {
          this.favoriteMessages.splice(favIndex, 1);
        }
        
        uni.showToast({
          title: 'Â∑≤ÂèñÊ∂àÊî∂Ëóè',
          icon: 'none',
          duration: 1500
        });
        console.log('[CHAT] Â∑≤ÂèñÊ∂àÊî∂Ëóè');
      }
      
      // ‰øùÂ≠òÊî∂ËóèÂàóË°®Âà∞Êú¨Âú∞
      this.saveFavorites();
      
      // ‰øùÂ≠òÊ∂àÊÅØÊõ¥Êñ∞
      this.saveAllMessages();
    },
    
    /**
     * Âà†Èô§Ê∂àÊÅØ
     */
    deleteMessage(index) {
      uni.showModal({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: 'Á°ÆÂÆöË¶ÅÂà†Èô§ËøôÊù°Ê∂àÊÅØÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            this.messages.splice(index, 1);
            this.saveAllMessages();
            
            uni.showToast({
              title: 'Â∑≤Âà†Èô§',
              icon: 'success',
              duration: 1500
            });
            console.log('[CHAT] Ê∂àÊÅØÂ∑≤Âà†Èô§');
          }
        }
      });
    },
    
    /**
     * ‰øùÂ≠òÊî∂ËóèÂàóË°®
     */
    saveFavorites() {
      try {
        uni.setStorageSync('chat_favorites', JSON.stringify(this.favoriteMessages));
        console.log('[CHAT] Êî∂ËóèÂàóË°®Â∑≤‰øùÂ≠ò');
      } catch (error) {
        console.error('[CHAT] ‰øùÂ≠òÊî∂ËóèÂàóË°®Â§±Ë¥•:', error);
      }
    },
    
    /**
     * Âä†ËΩΩÊî∂ËóèÂàóË°®
     */
    loadFavorites() {
      try {
        const favorites = uni.getStorageSync('chat_favorites');
        if (favorites) {
          this.favoriteMessages = JSON.parse(favorites);
          console.log(`[CHAT] Âä†ËΩΩ‰∫Ü${this.favoriteMessages.length}Êù°Êî∂ËóèÊ∂àÊÅØ`);
        }
      } catch (error) {
        console.error('[CHAT] Âä†ËΩΩÊî∂ËóèÂàóË°®Â§±Ë¥•:', error);
      }
    },
    
    /**
     * ÂàáÊç¢Ë°®ÊÉÖÈÄâÊã©Âô®ÊòæÁ§∫
     */
    toggleEmojiPicker() {
      this.showEmojiPicker = !this.showEmojiPicker;
      console.log(`[CHAT] Ë°®ÊÉÖÈÄâÊã©Âô®: ${this.showEmojiPicker ? 'ÊòæÁ§∫' : 'ÈöêËóè'}`);
    },
    
    /**
     * ÊèíÂÖ•Ë°®ÊÉÖ
     */
    insertEmoji(emoji) {
      this.inputText += emoji;
      // ÂÖ≥Èó≠Ë°®ÊÉÖÈÄâÊã©Âô®ÔºàÂèØÈÄâÔºâ
      // this.showEmojiPicker = false;
      console.log('[CHAT] ÊèíÂÖ•Ë°®ÊÉÖ:', emoji);
    },
    
    // ÂèëÈÄÅÊ∂àÊÅØ
    async sendMessage() {
      const text = this.inputText.trim();
      if (!text || this.isSending) return;
      
      // ÂàõÂª∫Áî®Êà∑Ê∂àÊÅØ
      const userMessage = {
        role: 'user',
        content: text,
        timestamp: Date.now()
      };
      
      // Ê∑ªÂä†Âà∞Ê∂àÊÅØÂàóË°®
      this.messages.push(userMessage);
      this.inputText = '';
      this.scrollToBottom();
      
      // ‰øùÂ≠òÁî®Êà∑Ê∂àÊÅØ
      await this.saveMessage(userMessage);
      
      // ÊòæÁ§∫ËæìÂÖ•‰∏≠Áä∂ÊÄÅ
      this.isSending = true;
      this.isTyping = true;
      
      try {
        // Ë∞ÉÁî®‰∫ëÂáΩÊï∞Ëé∑ÂèñAIÂõûÂ§ç
        const res = await uniCloud.callFunction({
          name: 'stress-chat',
          data: {
            messages: this.messages,
            stream: false
          }
        });
        
        // Ê∑ªÂä†AIÂõûÂ§çÊ∂àÊÅØ
        if (res.result && res.result.success && res.result.data) {
          const aiContent = res.result.data.content || res.result.data.message;
          this.addAIMessage(aiContent);
        } else {
          console.error('[CHAT] AIÂõûÂ§çÂºÇÂ∏∏:', res);
          this.addAIMessage('Êä±Ê≠âÔºåAIÊ≠£Âú®ÊÄùËÄÉ‰∏≠ÔºåËØ∑Á®çÂêéÂÜçËØï„ÄÇ');
        }
        
      } catch (error) {
        console.error('[CHAT] ÂèëÈÄÅÂ§±Ë¥•:', error);
        this.addAIMessage('Êä±Ê≠âÔºåÊàëÁé∞Âú®Êó†Ê≥ïÂõûÂ§ç„ÄÇËØ∑Á®çÂêéÂÜçËØï„ÄÇ');
      } finally {
        this.isSending = false;
        this.isTyping = false;
        this.scrollToBottom();
      }
    },
    
    // Ê®°ÊãüAIÂõûÂ§çÔºàÂºÄÂèëÈò∂ÊÆµ‰ΩøÁî®Ôºâ
    async simulateAIResponse(userMsg) {
      return new Promise((resolve) => {
        setTimeout(() => {
          let response = '';
          
          if (userMsg.includes('ÂéãÂäõ') || userMsg.includes('ÁÑ¶Ëôë')) {
            response = 'ÊàëÁêÜËß£ÊÇ®Áé∞Âú®ÊÑüÂà∞ÂéãÂäõÂæàÂ§ß„ÄÇÂéãÂäõÊòØÁîüÊ¥ª‰∏≠ÂæàÊ≠£Â∏∏ÁöÑ‰∏ÄÈÉ®ÂàÜÔºåÈáçË¶ÅÁöÑÊòØÂ≠¶‰ºöÂ¶Ç‰ΩïÁÆ°ÁêÜÂÆÉ„ÄÇÊÇ®ÂèØ‰ª•Â∞ùËØïÊ∑±ÂëºÂê∏„ÄÅÈÄÇÂ∫¶ËøêÂä®ÔºåÊàñËÄÖÊâæÊúãÂèãÂÄæËØâ„ÄÇËÆ∞‰ΩèÔºåÊÇ®‰∏çÊòØ‰∏Ä‰∏™‰∫∫Âú®Èù¢ÂØπËøô‰∫õ„ÄÇ';
          } else if (userMsg.includes('Áù°‰∏çÁùÄ') || userMsg.includes('Â§±Áú†')) {
            response = 'Áù°Áú†ÈóÆÈ¢òÁ°ÆÂÆûÂæàÂõ∞Êâ∞‰∫∫„ÄÇÂª∫ËÆÆÊÇ®Áù°ÂâçÈÅøÂÖç‰ΩøÁî®ÊâãÊú∫Ôºå‰øùÊåÅËßÑÂæãÁöÑ‰ΩúÊÅØÊó∂Èó¥„ÄÇÊÇ®‰πüÂèØ‰ª•Â∞ùËØïÊàë‰ª¨ÁöÑÂÜ•ÊÉ≥Èü≥ÁñóÂäüËÉΩÔºåÂ∏ÆÂä©ÊîæÊùæË∫´ÂøÉÔºåÊîπÂñÑÁù°Áú†Ë¥®Èáè„ÄÇ';
          } else if (userMsg.includes('Ë∞¢Ë∞¢') || userMsg.includes('ÊÑüË∞¢')) {
            response = '‰∏çÂÆ¢Ê∞îÔºÅÂæàÈ´òÂÖ¥ËÉΩÂ§üÂ∏ÆÂà∞ÊÇ®„ÄÇÂ¶ÇÊûúËøòÊúâ‰ªª‰ΩïÂõ∞Êâ∞ÔºåÈöèÊó∂ÂèØ‰ª•ÂíåÊàëËÅäËÅä„ÄÇËÆ∞ÂæóÁÖßÈ°æÂ•ΩËá™Â∑±ÔºÅ';
          } else {
            response = 'ÊàëÂê¨Âà∞‰∫ÜÊÇ®ÁöÑÂøÉÂ£∞„ÄÇËôΩÁÑ∂ÊàëÂè™ÊòØ‰∏Ä‰∏™AIÔºå‰ΩÜÊàëÁúüËØöÂú∞Â∏åÊúõËÉΩÁªôÊÇ®‰∏Ä‰∫õÊîØÊåÅ„ÄÇÂ¶ÇÊûúÊÇ®ÊÑøÊÑèÔºåÂèØ‰ª•ËØ¶ÁªÜËØ¥ËØ¥ÊÇ®ÁöÑÊÑüÂèóÔºåÊàë‰ºöËÆ§ÁúüÂÄæÂê¨„ÄÇ';
          }
          
          this.addAIMessage(response);
          resolve();
        }, 1500);
      });
    },
    
    // Ê∑ªÂä†AIÊ∂àÊÅØ
    async addAIMessage(content) {
      const aiMessage = {
        role: 'assistant',
        content: content,
        timestamp: Date.now()
      };
      
      this.messages.push(aiMessage);
      this.scrollToBottom();
      
      // ‰øùÂ≠òAIÊ∂àÊÅØ
      await this.saveMessage(aiMessage);
    },
    
    // ÊªöÂä®Âà∞Â∫ïÈÉ®
    scrollToBottom() {
      this.$nextTick(() => {
        this.scrollIntoView = this.getMsgId(this.messages.length - 1);
      });
    },
    
    // Ëé∑ÂèñÊ∂àÊÅØID
    getMsgId(index) {
      return this.msgIdPrefix + index;
    },
    
    /**
     * Â§ÑÁêÜÊ∏ÖÁ©∫ËÅäÂ§©
     */
    handleClearChat() {
      if (this.messages.length === 0) {
        uni.showToast({
          title: 'ÊöÇÊó†ËÅäÂ§©ËÆ∞ÂΩï',
          icon: 'none'
        });
        return;
      }
      
      uni.showModal({
        title: 'Ê∏ÖÁ©∫ËÅäÂ§©ËÆ∞ÂΩï',
        content: 'Á°ÆÂÆöË¶ÅÊ∏ÖÁ©∫ÊâÄÊúâËÅäÂ§©ËÆ∞ÂΩïÂêóÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊÅ¢Â§ç„ÄÇ',
        confirmText: 'Á°ÆÂÆöÊ∏ÖÁ©∫',
        confirmColor: '#DC3545',
        success: (res) => {
          if (res.confirm) {
            this.clearCurrentSession();
          }
        }
      });
    }
  }
}
</script>

